body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
	position: relative;
	display: block;
	width: 100vw;
	height: 100vh;
	background: -webkit-linear-gradient(#e66465, #9198e5);
	background: linear-gradient(#e66465, #9198e5);
}
.App {
	background-color: rgb(226, 238, 210);
	position: absolute;
	width: 500px;
	min-height: 600px;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
	text-align: center;
	padding: 10px;
	box-shadow: 10px 10px rgba(0, 0, 0, .5);
}
h1 {
	margin: 0 0 10px;
	color: lightsalmon;
	text-shadow: 2px 2px black;
	font-size: 30px;
	font-style: italic;
}
.deleteBtn {
	width: 19px;
	height: 19px;
	border: 2px solid black;
	line-height: 10px;
	color: white;
	background-color: black;
	font-weight: bold;
	cursor: pointer;
	border-radius: 50%;
	box-shadow: 1px 1px rgba(0, 0, 0, 0.5);
}
.doneBtn,
.addBtn {
	border-radius: 3px;
	cursor: pointer;
	height: 19px;
	border: 2px solid lightgreen;
	background-color: lightgreen;
	box-shadow: 1px 1px rgba(0, 0, 0, 0.5);
}
.addBtn {
	display: block;
	margin: 0 auto;
	width: 60px;
	height: 25px;
}
footer {
	position: absolute;
	bottom: 0;
	width: 100%;
	height: 6vh;
	background-color: black;
}

footer span {
	display: block;
	color: white;
	width: 100%;
	line-height: 6vh;
	text-align: center;
	font-family: sans-serif;
	font-size: 15px;
}

label {
	margin: 0 5px;
	height: 20px;
}
input {
	margin: 5px;
	padding: 0 5px;
	font-size: 15px;
}
.form {
	width: 60%;
	margin: 15px auto;
	font-size: 15px;
	text-align: left;
}

.active,
.done {
	padding: 5px 15px 15px;
	text-align: left;
	font-size: 15px;
}
.active h3,
.done h3 {
	text-align: center;
	padding: 0 5px 8px 5px;
}

